import misEnum from '../../common/mis-enum.js';
export default {
	data() {
		return {
			StatusEnumMap: [],
			steps: [],
			color: '',
			info: {},
			require: "无",
			showModal: false,
			failReason: ""
			
		}
	},
	onLoad(option) {
		global.$http.post('/car/apply/getInfo', {
			params: {
				apply_id: option.id
			},
		}).then(res => {
			if (res.status === "0") {
				this.info = res.data;
				this.StatusEnumMap = misEnum.UseCarRecordEnumMap;
				this.showSteps();
				//设置用车要求
				this.info.nums.forEach(c=>{
					if(c.num > 0)
					{
						if(this.require == "无")
						{
							this.require = c.car_type+c.num+"辆";
						}else{
							this.require += "; " + c.car_type+c.num+"辆";
						}
					}
				});
			} else {
				uni.showToast({
					title: res.msg,
					icon: 'none'
				});
			}
		}).catch(err => {
			uni.showToast({
				title: err.message,
				icon: 'none'
			});
		});
	},
	methods: {
		showSteps() {
			//设置进度步骤
			this.steps.length = 0;
			switch (this.info.status) {
				case 0:
					this.steps.push({
						name: this.StatusEnumMap.get(0),
						color: 'text-green'
					});
					this.steps.push({
						name: "审批",
						color: ''
					});
					this.steps.push({
						name: "调度",
						color: ''
					});
					this.steps.push({
						name: "订单进行中",
						color: ''
					});
					this.steps.push({
						name: "订单完成",
						color: ''
					});
					break;
				case 1:
					this.steps.push({
						name: this.StatusEnumMap.get(0),
						color: 'text-green'
					});
					this.steps.push({
						name: this.StatusEnumMap.get(1),
						color: 'text-green'
					});
					this.steps.push({
						name: "调度",
						color: ''
					});
					this.steps.push({
						name: "订单进行中",
						color: ''
					});
					this.steps.push({
						name: "订单完成",
						color: ''
					});
					break;
				case -1:
					this.steps.push({
						name: this.StatusEnumMap.get(0),
						color: 'text-green'
					});
					this.steps.push({
						name: this.StatusEnumMap.get(-1),
						color: 'text-red'
					});
					this.steps.push({
						name: "调度",
						color: ''
					});
					this.steps.push({
						name: "订单进行中",
						color: ''
					});
					this.steps.push({
						name: "订单完成",
						color: ''
					});
					break;
				case 2:
					this.steps.push({
						name: this.StatusEnumMap.get(0),
						color: 'text-green'
					});
					this.steps.push({
						name: this.StatusEnumMap.get(1),
						color: 'text-green'
					});
					this.steps.push({
						name: this.StatusEnumMap.get(2),
						color: 'text-green'
					});
					this.steps.push({
						name: "订单进行中",
						color: ''
					});
					this.steps.push({
						name: "订单完成",
						color: ''
					});
					break;
				case -2:
					this.steps.push({
						name: this.StatusEnumMap.get(0),
						color: 'text-green'
					});
					this.steps.push({
						name: this.StatusEnumMap.get(1),
						color: 'text-green'
					});
					this.steps.push({
						name: this.StatusEnumMap.get(-2),
						color: 'text-red'
					});
					this.steps.push({
						name: "订单进行中",
						color: ''
					});
					this.steps.push({
						name: "订单完成",
						color: ''
					});
					break;
				case 3:
					this.steps.push({
						name: this.StatusEnumMap.get(0),
						color: 'text-green'
					});
					this.steps.push({
						name: this.StatusEnumMap.get(1),
						color: 'text-green'
					});
					this.steps.push({
						name: this.StatusEnumMap.get(2),
						color: 'text-green'
					});
					this.steps.push({
						name: this.StatusEnumMap.get(3),
						color: 'text-green'
					});
					this.steps.push({
						name: "订单完成",
						color: ''
					});
					break;
				case -3:
					this.steps.push({
						name: this.StatusEnumMap.get(0),
						color: 'text-green'
					});
					this.steps.push({
						name: this.StatusEnumMap.get(-3),
						color: 'text-red'
					});
					break;
				case 4:
					this.steps.push({
						name: this.StatusEnumMap.get(0),
						color: 'text-green'
					});
					this.steps.push({
						name: this.StatusEnumMap.get(1),
						color: 'text-green'
					});
					this.steps.push({
						name: this.StatusEnumMap.get(2),
						color: 'text-green'
					});
					this.steps.push({
						name: this.StatusEnumMap.get(3),
						color: 'text-green'
					});
					this.steps.push({
						name: this.StatusEnumMap.get(4),
						color: 'text-green'
					});
					break;
			}
		},
		verify(status, failReason) {
			uni.showLoading({
				title: '提交中',
				mask: false
			});
			global.$http.post('/car/apply/sp', {
				params: {
					apply_id: this.info.id,
					status: status,
					fail_reason: failReason
				},
			}).then(res => {
				uni.hideLoading();
				if (res.status === "0") {
					uni.showToast({
						title: "提交成功",
						icon: 'none'
					});
					this.info.status = status;
					this.showSteps();
				} else {
					uni.showToast({
						title: res.msg,
						icon: 'none'
					});
				}
			}).catch(err => {
				uni.hideLoading();
				uni.showToast({
					title: err.message,
					icon: 'none'
				});
			});
		},
		verifySuccess: function() {
			uni.showModal({
				title: '提示',
				content: '确定审批通过？',
				showCancel: true,
				cancelText: '取消',
				confirmText: '确定',
				success: res => {
					if (res.cancel) return;
					this.verify(1, "");
				},
				fail: () => {},
				complete: () => {}
			});
		},
		verifyFail:function(){
			this.showModal = true;
		},
		hideModal: function(){
			this.showModal = false;
		},
		fillFailReason: function(e){
			this.failReason = e.detail.value;
		},
		sureModal:function(){
			if(this.failReason.length <= 0){
				uni.showToast({
					title: "请填写不通过原因",
					icon: 'none'
				});
				return;
			}
			this.verify(-1, this.failReason)
			this.showModal = false;
		}
	}
}
